<template>
	<div class="videoPlay">
		<div ref="myVideoPlay"></div>
		<form action="" method="get">
			<input type="text" name="torrent" placeholder="torrent"/>
			<input type="submit" name="" id="" value="提交" />
		</form>
		<div class="log"></div>
		<ul>
			<li>
				<label for="">当前文件大小：</label>
				<span>{{torrentObj.total}}</span>
			</li>
			<li>
				<label for="">进度：</label>
				<span>{{torrentObj.progressBar}}</span>
			</li>
			<li>
				<label for="">?：</label>
				<span>{{torrentObj.remaining}}</span>
			</li>
			<li>
				<label for="">网络状态:</label>
				<span>{{torrentObj.numPeers}}</span>
			</li>
			<li>
				<label for="">下载速度：</label>
				<span>{{torrentObj.downloadSpeed}}</span>
			</li>
			<li>
				<label for="">上传速度：</label>
				<span>{{torrentObj.uploadSpeed}}</span>
			</li>
			<li>
				<label for="">已下载：</label>
				<span>{{torrentObj.downloaded}}</span>
			</li>
		</ul>
	</div>
</template>

<script>
	import 'DPlayer/dist/DPlayer.min.css';
	import DPlayer from 'DPlayer';
	import WebTorrent from 'webtorrent';
	import dragDrop from 'drag-drop';
	export default {
		data(){
			return {
				torrentObj: {
					total: '',
					progressBar: '',
					remaining: '',
					numPeers: '',
					downloadSpeed: '',
					uploadSpeed: '',
					downloaded: '',
					torrentList: []
				},
				torrent: '',
				dp: null,
				options: {
				    container: '',
				    autoplay: false,
				    theme: '#FADFA3',
				    loop: true,
				    lang: 'zh-cn',
				    screenshot: true,
				    hotkey: true,
				    preload: 'auto',
				    logo: 'logo.png',
				    volume: 0.7,
				    mutex: true,
				    video: {
				    	url: '',
//				        url: 'http://1251883823.vod2.myqcloud.com/6b94ca32vodsh1251883823/d744a6895285890782304152843/mjQcm1ClBSQA.mp4',
				        pic: 'dplayer.png',
				        thumbnails: 'thumbnails.jpg',
				        type: 'auto'
				    },
				    subtitle: {
				        url: 'dplayer.vtt',
				        type: 'webvtt',
				        fontSize: '25px',
				        bottom: '10%',
				        color: '#b7daff'
				    },
				    danmaku: {
				        id: '9E2E3368B56CDBB4',
//				        api: 'https://api.prprpr.me/dplayer/',
				        token: 'tokendemo',
				        maximum: 1000,
//				        addition: ['https://api.prprpr.me/dplayer/v3/bilibili?aid=4157142'],
				        user: 'DIYgod',
				        bottom: '15%',
				        unlimited: true
				    },
				    contextmenu: [
				        {
				            text: 'custom1',
//				            link: 'https://github.com/DIYgod/DPlayer'
				        },
				        {
				            text: 'custom2',
				            click: (player) => {
				                console.log(player);
				            }
				        }
				    ],
				    highlight: [
				        {
				            text: 'marker for 20s',
				            time: 20
				        },
				        {
				            text: 'marker for 2mins',
				            time: 120
				        }
				    ]
				}
			}
		},
		mounted(){
			this.setBlobUrl();
			this.options.container = this.$refs.myVideoPlay;
			this.dp = new DPlayer(this.options);
		},
		methods:{
			setBlobUrl(){
				let _this = this;
				let announceList = [
					["udp://tracker.leechers-paradise.org:6969"],
					["udp://tracker.coppersurfer.tk:6969"],
					["udp://tracker.opentrackr.org:1337"],
					["udp://explodie.org:6969"],
					["udp://tracker.empire-js.us:1337"],
					["wss://tracker.btorrent.xyz"],
					["wss://tracker.openwebtorrent.com"],
					["wss://tracker.fastcast.nz"]
				];
				global.WEBTORRENT_ANNOUNCE = announceList.map(function(e) {
					return e[0]
				}).filter(function(e) {
					return 0 === e.indexOf("wss://") || 0 === e.indexOf("ws://")
				});
				
				let client = new WebTorrent();
				dragDrop('body', function (files) {
				 	client.seed(files, function (torrent) {
				   		console.log('Client is seeding ' + torrent.magnetURI)
				  	})
				});
				
				client.on('error', function (err) {
			        console.error('ERROR: ' + err.message)
			    })
		
		      document.querySelector('form').addEventListener('submit', function (e) {
		        e.preventDefault();
		
		        var torrentId = document.querySelector('form input[name=torrent]').value
		        log('Adding ' + torrentId)
		        client.add(torrentId, onTorrent)
		      })
		
		      function onTorrent (torrent) {
		        var interval = setInterval(onProgress, 5000)
		
		        torrent.on('done', function () {
		          log('Progress: 100%')
		          clearInterval(interval)
//		          onProgress();
		        })
		
		        torrent.files.forEach(function (file) {
		          file.getBlobURL(function (err, url) {
		          	
		            if (err){
		            	return log(err.message)
		            }
		            if(url !=''){
		            	_this.options.video.url = url;	
		            }
		          })
		        })
		        
		        var file = torrent.files.find(function (file) {
          			return file.name.endsWith('.mp4')
    			})
		        
        		// Stream the file in the browser
				_this.dp.pause();
				_this.dp.switchVideo({
				    url: file._torrent.urlList[0] + file.path,
//				    pic: 'second.png',
//				    thumbnails: 'second.jpg'
				});
				_this.dp.play();

//		        setInterval(onProgress, 500)
		        onProgress()

        		// Statistics
       			function onProgress () {
          			// Peers
//        			_this.torrentObj.numPeers = torrent.numPeers + (torrent.numPeers === 1 ? ' peer' : ' peers')
					
					_this.torrentObj.numPeers = torrent.numPeers; //1正常 0连接失败

		          	// Progress
		          	var percent = Math.round(torrent.progress * 100 * 100) / 100
		          	_this.torrentObj.progressBar= percent + '%'
		          	_this.torrentObj.downloaded = prettyBytes(torrent.downloaded)
		          	_this.torrentObj.total = prettyBytes(torrent.length)

      				// Remaining time
          			var remaining;
          			
		          	if (torrent.done) {
		            	remaining = 'Done.'
		          	} else {
		            	remaining = moment.duration(torrent.timeRemaining / 1000, 'seconds').humanize()
		            	remaining = remaining[0].toUpperCase() + remaining.substring(1) + ' remaining.'
		          	}
          			_this.torrentObj.remaining = remaining

          			// Speed rates
		          	_this.torrentObj.downloadSpeed = prettyBytes(torrent.downloadSpeed) + '/s'
		          	_this.torrentObj.uploadSpeed = prettyBytes(torrent.uploadSpeed) + '/s'
        		}
		      	}
		
		      	function log (str) {
			        _this.torrentObj.torrentList.push(str);
		      	}
		      	
		      	function prettyBytes(num) {
			        var exponent, unit, neg = num < 0, units = ['B', 'kB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB']
			        if (neg) num = -num
			        if (num < 1) return (neg ? '-' : '') + num + ' B'
			        exponent = Math.min(Math.floor(Math.log(num) / Math.log(1000)), units.length - 1)
			        num = Number((num / Math.pow(1000, exponent)).toFixed(2))
			        unit = units[exponent]
			        return (neg ? '-' : '') + num + ' ' + unit
		      	}
			}
		}
	}
</script>
<style>
 
</style>


