<template>
	<div class="player" :class="!isVideo?'maxH':''">
		<van-icon name="arrow-left" class="return_icon" @click="handleReturn" v-show="isVideo"/>
		<div ref="play" class="play"></div>
	</div>
</template>

<script>
	import 'DPlayer/dist/DPlayer.min.css';
	
//	import Hls from "hls";
//	import "../../static/js/dash.js";
//	import "../../static/js/hls.min.js";
//	import webtorrent from "webtorrent";  
	import DPlayer from 'DPlayer';
	import video from "../../static/js/video.json";
	export default {
		props: ["animateID","videoIndex"],
		data(){
			return {
				videoList: [],
				dp: null,
				videoUrl: '',
				isVideo: false,
				iconShow: true,
				options: {
				    container: '',
				    autoplay: false,
				    hotkey: true,
				    screenshot: true,
				    contextmenu: [],
				    video: {
				    	url: '',
				    	pic: require('../../static/images/time.gif'),
				        type: 'auto',
				         customType: {
				            'customHls': function (video, player) {
				            	if(video.src!=""){
			            			const hls = new Hls();
					                hls.loadSource(video.src);
					                hls.attachMedia(video);
				            	}
				                
				            }
				        }
				    }
				}
			}
		},
		watch: {
			videoIndex(newVal,oldVal){
//				console.log(newVal)
				if(this.isVideo && newVal != oldVal){
					this.selectVideo(newVal);
				}
			},
			videoUrl(newVal,oldVal){
//				console.log(newVal)
				if(newVal != "" && newVal != oldVal){
					this.dp.pause();
					this.dp.switchVideo({
					    url: newVal,
					     type: 'auto',
				         customType: {
				            'customHls': function (video, player) {
				            	if(video.src!=""){
			            			const hls = new Hls();
					                hls.loadSource(video.src);
					                hls.attachMedia(video);
				            	}
				                
				            }
				        }
					});
					this.dp.play();
				}
			}
		},
		created(){
			this.videoList = video.videoJSON;	
		},
		mounted(){
			this.$nextTick(()=>{
				this.selectVideo(this.videoIndex);
				this.newPlay();
				this.dp.on('fullscreen', () => {
//				    console.log('player ended');
					this.iconShow = false;
				});
				this.dp.on('fullscreen_cancel', () => {
//				    console.log('player ended');
					this.iconShow = true;
				});
			});
		},
		methods:{
			handleReturn(){
    			let historyLength = window.history.length;
    			if(window.history.length <=1){
    				this.$router.push({
    					path: '/public.home'
    				});
    			}else{
    				this.$router.go(-1);
    			}
    		},
			selectVideo(keys){
				for(let i = 0; i< this.videoList.length; i++){
					if(this.videoList[i].animate == this.animateID){
						for(let video in this.videoList[i].list){
//							if(video == this.videoIndex){
								this.videoUrl = this.videoList[i].list[keys];
								this.options.video.url = this.videoUrl;
//							}
						}
					}
				};
			},
			newPlay(){
				this.options.container = this.$refs.play;
				this.dp = new DPlayer(this.options);
				this.isVideo = true;
			}
		}
	}
</script>

<style lang="less">
	.player{
		position: relative;
		/*overflow: hidden;*/
		.return_icon{
			position: absolute;
			font-size: .4rem;
			color: #fff;
			top: .5rem;
			left: .5rem;
			z-index: 200000;
		}
	}
	.maxH{
		height: 4rem;
	}
</style>


