export default {
	//获取acg列表
  saveAcgList:(state,acgObject)=>{
  	Array.prototype.acgObject = acgObject
    acgObject?state.acgObj=Array.prototype.acgObject:{}
  },
  //获取每周节目
  saveWeekList:(state,weekObject)=>{
  	Array.prototype.weekObject = weekObject
    weekObject?state.weekObj=Array.prototype.weekObject:{}
  },
  setplayDetails:(state,playObject)=>{
  	Array.prototype.playObject = playObject
    playObject?state.playObj=Array.prototype.playObject:{}
  }
}



