<template>
	<div class="dmzjIframe">
		<!--<iframe src="https://www.dmzj.com/index.html" 
     		width="100%"
     		height="100%"
     		frameborder="no"
     		name="dmzj"
     		scrolling="auto"
     		class="dmzj"
     		allowfullscreen="true"></iframe>-->
	</div>
</template>

<script>
	export default {
		data(){
			return {
				dmzjObj: {
					swiper:[{
						url: "",
						title: "",
						a: "",
					}]
				}
			}
		},
		mounted(){
			
			this.getIframe().then( res => {
				window.admin = "dmzj.com"
				if(document.readyState == "complete"){
				    //在此处访问并操作iframe所引入的document文档对象
				    let iwindow = res.contentWindow;
				let idoc = res.contentDocument;
				console.log(idoc)
				this.$emit("load",this.dmzjObj)
				}
				
			});
		},
		methods:{
			getIframe(){
				var url="https://www.dmzj.com";//跨域url  
			    var param="";  
			    var ifr=document.createElement("iframe");//以下创建一个隐藏的iframe  
			    ifr.setAttribute("width",0);  
			    ifr.setAttribute("height",0);  
			    ifr.setAttribute("class","dmzj");  
			    this.$(".dmzjIframe").append(ifr) 
			    ifr.src=encodeURI(url+param);  
				return new Promise((resolve,reject)=>{
					(function(that){
						let dmzj = "";
						 that.$(document).ready(function(e){
					    　　    var iframe = that.$(".dmzj")[0];
					        if (iframe.attachEvent) {   
					            iframe.attachEvent("onload", function() {    
					                //iframe加载完成后你需要进行的操作  
				                 	dmzj = iframe;
					                resolve(dmzj);
					            });    
					        } else {  
					            iframe.onload = function() {    
			                        //iframe加载完成后你需要进行的操作  
			                        dmzj = iframe;
			                        resolve(dmzj);
					            };    
					        } 
				        })
					})(this);
				})
			}
		}
	}
</script>

<style lang="less">
	.dmzj{
		height: 100%;
	}
</style>


