<template>
	<div class="playdetails">
		<player :animateID="playdet.animeId" :videoIndex="active"></player>
		<van-cell-group v-if="playdet">
		  <van-cell class="van_cell">
		  	<van-row>
			  <van-col span="8">
			  	<div class="svg-content" @click="doShare">
			  	<img src="../../static/svg/share-icon.svg" alt="share" class="svg_icon">分享
			  	</div>
			  </van-col>
			  <van-col span="8">
			  	<div @click="islick = !islick" class="svg-content">
				  	<img :src="islick?require('../../static/svg/lick-icon.svg'):require('../../static/svg/active-lick-icon.svg')" alt="share" class="svg_icon">追番
			  	</div>
			  </van-col>
			  <van-col span="8">
			  	<div class="svg-content">
			  		<img src="../../static/svg/download-icon.svg" alt="share" class="svg_icon">下载
			  	</div>
		  	  </van-col>
			</van-row>
	  	 </van-cell>
		  <van-cell>
		  	<van-row class="selectpro">
			  <van-col span="8">选集</van-col>
			  <!--<van-col span="8">span: 8</van-col>-->
			  <van-col span="10" offset="6" class="newpro">
			  	<span v-if="playdet.episodes && playdet.episodes.length > 0">更新至第{{playdet.episodes.length}}话</span>
			  	<van-icon name="arrow" class="van-cell__right-icon" @click="isMore = true"/>
			  </van-col>
			</van-row>
			<div class="play_scroll">
				<ul>
					<li v-for="(item,keys) in playdet.episodes" @click="playIndex(keys)" :class="active == keys?'active_list':''">
						<span>{{item.episodeTitle.split(" ")[0]}}</span>
			  			<p>{{item.episodeTitle.split(" ")[1]}}</p>
					</li>
				</ul>
			</div>
		  </van-cell>
		</van-cell-group>
		<van-row class="playdet_msg">
		  <van-col span="6">
		  	<img v-lazy="playdet.imageUrl" alt="" />
		  </van-col>
		  <van-col span="14">
		  	<label for="" class="playdet_title">{{playdet.animeTitle}}</label>
		  	<div><label for="">视频类型：</label><span>{{playdet.typeDescription}}</span></div>
		  	<p>{{playdet.summary}}</p>
		  </van-col>
		  <van-col span="4">
		  	<span>{{playdet.rating}}</span><span>分</span>
		  </van-col>
		</van-row>
		<van-cell-group>
			<van-cell>
			  	<van-row class="selectpro">
				  <van-col span="8">更多推荐</van-col>
				  <!--<van-col span="8">span: 8</van-col>-->
				  <van-col span="10" offset="6" class="newpro">
				  	<span>换一换</span>
				  	<van-icon name="arrow" class="van-cell__right-icon" @click="resetRes"/>
				  </van-col>
				</van-row>
			  </van-cell>
			  	<van-swipe ref="van_swiper" @change="onChange" :show-indicators="false" :touchable="false" :loop="false">
				  <van-swipe-item
				  	v-for="res in playdet.resList">
					  	<van-cell>
						  	<van-row>
							  <van-col span="8" v-for="item in res" class="pro_rec">
							  	<div>
							  		<img v-lazy="item.imageUrl" alt="" />
							  		<span>{{item.rating}}</span>
							  	</div>
							  	<p>{{item.animeTitle}}</p>
							  </van-col>
							</van-row>
					  	</van-cell>
				  	</van-swipe-item>				
				  <!--<template slot="indicator">
				    <div class="custom-indicator">
				      {{ this.current + 1 }}/4
				    </div>
				  </template>-->
				</van-swipe>
				
			  <!---->
	  	 </van-cell-group>
		<van-popup v-model="isMore" position="bottom" :overlay="true" class="van_popup_more">
			<van-cell-group>
			  <van-cell title="选集">
			  	<van-icon slot="right-icon" class="van-cell__right-icon"  name="close" @click="isMore = false"/>
			  </van-cell>
			</van-cell-group>
			<div class="van_panel_content">
				<van-button @click="selectpro(item,keys)" type="default" v-for="(item,keys) in playdet.episodes">{{keys + 1}}</van-button>
			</div>
		</van-popup>
		<div id="share_box" style="display:none;">
		    <h1 class="share_title">分享到</h1>
		    <div 
		    	class="social-share"
		    	data-initialized="true" 
		    	style="text-align: center;" 
		    	data-url="`${window.location.href}`" 
		    	data-title="`${document.title}`" 
		    	data-description="`${playdet.animeTitle}`" 
		    	data-image="`${playdet.imageUrl}`">
		        <a href="#" class="social-share-icon icon-weibo"></a>
		        <a href="#"class="social-share-icon icon-wechat"></a>
		        <a href="#" class="social-share-icon icon-qq"></a>
		        <a href="#" class="social-share-icon icon-qzone"></a>
		    </div>
		</div>
	</div>
</template>

<script>
	import $ from 'jquery';
	import player from './player'
	import { mapState } from 'vuex';
	export default {
		components: {
			player
		},
		data(){
			return {
				playdet: {},
				active: 0,
				isMore: false,
				swipercurrent: 0,
				islick: true
			}
		},
		computed: {
			...mapState(["playObj"])
		},
		mounted(){
			this.active = this.$route.query.playIndex;
			this.playdet = Object.assign(
				{},this.playObj,{
					resList: []
				});
				if(this.playdet.similars.length >0){
					this.playdet.resList = this.split_arr(this.playdet.similars, 3);
				}
		},
		methods:{
			doShare(){
				layer.open({
				    type: 1,
				    content: $('#share_box').html(),
				    anim: 'up',
				    style: 'position:fixed; bottom:0; left:0; width: 100%; padding:10px 0; border:none;'
			    });
			},
			playIndex(keys){
				this.active = keys;
			},
			split_arr(arr,len){
				var a_len = arr.length;
				var result = [];
				for(var i=0;i<a_len;i+=len){
					result.push(arr.slice(i,i+len));
				}    
				return result;
			},
			selectpro(obj,keys){
				this.active = keys;
//				alert(keys)
				this.isMore = false;
			},
			onChange(index) {
		      this.swipercurrent = index;
		   	},
			resetRes(){
				if(this.swipercurrent >= this.playdet.resList.length){
					this.swipercurrent = 0;
				}else{
					this.swipercurrent+=1;
				}
				this.$refs.van_swiper.swipeTo(this.swipercurrent)
			}
		}
	}
</script>

<style lang="less">
	.playdetails {
		height: 100vh;
		background-color: #EEEEEE;
		.selectpro{
			>div{
				text-align: left;
			}
			.newpro{
				text-align: right;
				overflow: hidden;
			span{
				}
				>i{
					float: right;
				}
			}
		}
		
		.van-cell{
			>div{
				width: 100%;
				text-align: center;
			}
		}
		.play_scroll{
			width: 100%;
			margin-top: .3rem;
			margin-bottom: .2rem;
			overflow-x: auto;
			>ul{
				width: max-content;
			    -webkit-margin-before: 0;
			    -webkit-margin-after: 0;
			    -webkit-margin-start: 0px;
			    -webkit-margin-end: 0px;
			    -webkit-padding-start: 0;
			    overflow: hidden;
				>li {
					text-align: left;
					width: 3rem;
					height: 1.5rem;
					padding: .1rem .2rem .1rem .2rem;
					box-sizing: content-box;
					font-size: .28rem;
				    border: 1px solid #eee;
				    border-radius: .05rem;
					box-sizing: border-box;
					float: left;
					margin-right: .5rem;
					>span{
						
					}
					>p{
						line-height: 1.5;
						overflow:hidden;
						text-overflow: ellipsis;
						display:-webkit-box;
						-webkit-line-clamp:2;
						-webkit-box-orient:vertical;
						margin: 0;
						padding: 0;
						text-align: left;
					}
				}
				.active_list{
					border: 1px solid #3388FF;
					color: #3388FF;
				}
			}
		}
		.playdet_msg{
			background: #fff;
			margin: .2rem 0;
			padding: .2rem;
			>div:nth-of-type(1){
				>img{
					width: 100%;
					border-radius: .1rem;
				}
			}
			>div:nth-of-type(2){
				padding-left: .3rem;
				text-align: left;
				>label {
					font-size: .36rem;
					overflow:hidden;
					text-overflow: ellipsis;
					display:-webkit-box;
					-webkit-line-clamp:1;
					-webkit-box-orient:vertical;
					margin: 0;
					padding: 0;
					text-align: left;
				}
				>div{
					margin: .15rem 0 .5rem 0;
					font-size: .24rem;
					font-size: #f6f6f6;
				}
				>p{
					font-size: .24rem;
					overflow:hidden;
					text-overflow: ellipsis;
					display:-webkit-box;
					-webkit-line-clamp:2;
					-webkit-box-orient:vertical;
					margin: 0;
					padding: 0;
					/*text-align: left;*/
					
				}
			}
			>div:nth-of-type(3){
				color: 	#FFD700;
				>span:nth-of-type(1){
					font-size: .36rem;
					margin-right: .05rem;
				}
			}
		}
		.van_popup_more{
			height: 50%;
			.van-cell{
				.van-cell__title{
					text-align: left;
				}
			}
			.van_panel_content{
				overflow: hidden;
				padding-bottom: .5rem;
				padding-left: .4rem;
				>button{
					width: 1rem;
					height: .6rem;
					line-height: .6rem;
					float: left;
					margin: .5rem .4rem 0 0;
				}
			}
		}
		.pro_rec{
			padding: .1rem;
			>div:nth-of-type(1){
				position: relative;
				overflow: hidden;
				height: 3rem;
				>img{
					height: 100%;
				}
				>span{
					font-size: .24rem;
					width: 5rem;
					height: 2rem;
					line-height: 3.3rem;
					position: absolute;
				    top: -1rem;
				    left: -3.5rem;
				    right: -5.5rem;
					margin: auto;
					background: rgba(255,215,0,0.5);
					color: #fff;
					transform: rotate(45deg);
				}
			}
			>p:nth-of-type(1){
				margin: .1rem 0;
				font-size: .24rem;
				overflow:hidden;
				text-overflow: ellipsis;
				display:-webkit-box;
				-webkit-line-clamp:2;
				-webkit-box-orient:vertical;
				margin: 0;
				padding: 0;
			}
		}
		.van_cell{
			.svg-content{
				display: flex;
				justify-content: center;
				align-items: center;
				.svg_icon{
					height: .4rem;
					margin-right: .1rem;
				}
			}
		}
	}
	.social-share{
		.social-share-icon{
			width: 50px !important;
			height: 50px !important;
			line-height: 50px !important;
			margin-left: 0.5rem;
		}
	}
	.share_title{
		color:#c01414;
		text-align: center;
		font-size: 0.38rem;
		line-height: 1rem;
	}
</style>


